@import url("https://fonts.googleapis.com/css2?family=Poppins:wght@300;500;600;700;900&display=swap");
* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

body {
  display: flex;
  justify-content: center;
  align-items: center;
  font-family: "Poppins", sans-serif;
  background-color: #3A3A3A;
}

#root {
  width: 100%;
  height: 100%;
}

.hidden {
  display: none !important;
}

.isInvalid {
  outline: 1px solid red !important;
}

.App .home {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  width: 90%;
  margin-top: 100px;
}
.App .home .anime-label-image {
  border-radius: 15px;
  width: 75%;
}

.App .home .anime-info-label {
  text-align: center;
  margin-top: 50px;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}
.App .home .anime-info-label .label {
  color: #DE0606;
  font-size: 1.5rem;
  font-weight: 700;
}
.App .home .anime-info-label .label-header {
  font-size: 1.7rem;
  color: #FFFFFF;
  margin: 5px;
}
.App .home .anime-info-label .label-description {
  font-size: 1.2rem;
  color: #FFFFFF;
  margin-top: 50px;
  font-weight: 300;
}

.App .genre-and-status-component {
  width: 90%;
}
.App .genre-and-status-component .genre-and-status-wrapper {
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  justify-content: space-around;
}
.App .genre-and-status-component .genre-and-status-wrapper .genre-and-status-element {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  margin: 10px 10px;
}
.App .genre-and-status-component .genre-and-status-wrapper .genre-and-status-element .header {
  font-size: 1.5rem;
  font-weight: 500;
  color: #FFFFFF;
}
.App .genre-and-status-component .genre-and-status-wrapper .genre-and-status-element .p-name {
  color: #FFFFFF;
  font-weight: 300;
}
.App .genre-and-status-component .genre-and-status-wrapper .genre-and-status-element .result-wrapper {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  justify-content: space-around;
  flex-wrap: wrap;
}
.App .genre-and-status-component .genre-and-status-wrapper .genre-and-status-element .result-wrapper .small-container {
  color: #FFFFFF;
  font-weight: 300;
  display: flex;
  justify-content: center;
  align-items: center;
}
.App .genre-and-status-component .genre-and-status-wrapper .genre-and-status-element .result-wrapper .small-container .vertical-line {
  width: 1px;
  height: 15px;
  border-left: 1px solid white;
  margin: 10px;
}

.App .anime-list-page {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  flex-wrap: wrap;
  width: 100%;
  margin-top: 40px;
}
.App .anime-list-page .cards-container {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
}

.App {
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

.App .nav {
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  justify-content: space-between;
  position: -webkit-sticky;
  position: sticky;
  top: 30px;
  left: 0;
  padding: 0 20px;
}
.App .nav .logo {
  width: 30px;
}

.App .search-field {
  width: 100%;
  height: 100vh;
  position: fixed;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  justify-content: space-between;
  top: 0;
  left: 0;
  z-index: 10;
  background-color: #B4B4B4;
}

.App .search-field .search-result {
  width: 95%;
  height: 87%;
  overflow: scroll;
  z-index: 100;
  padding: 10px 0;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  text-align: center;
}/*# sourceMappingURL=index.css.map */